import styled from 'styled-components';

export const StyledHeader = styled.header`
background-color: #4674a0;
margin: 0;
padding: 0;
position: relative;

 h1 {
   text-align: center;
   font-family: sans-serif;
   font-size: 2.5vw;
   background-color: red;
   border: solid 0.2vw rgba(210, 0, 0, 0.5);
   padding: 0.5vw;
   border-radius: 0.5vw;
 }

 h3 {
  text-align: center;
  background-color: white;
  font-size: 2vw;
  border: solid 0.2vw rgba(210, 210, 210, 0.5);
  padding: 0.5vw;
  border-radius: 0.5vw;
 }

 h4 {
  text-align: center;
  background-color: midnightblue;
  color: white;
  font-size: 1.5vw;
  border: solid 0.2vw rgba(18, 18, 105, 0.5);
  padding: 0.5vw;
  border-radius: 0.5vw;
 }

 h5 {
   color: white;
   font-size: 1vw;
 }

`;

export const StyledBody = styled.body`
background-color: lightblue;
margin: 0 0 0 1vw;
padding: 0;
position: relative;

h2 {
  font-family: Arial;
  font-size: 2vw;
}

p {
  font-size: 1.6vw;
}

p > p {
  font-family: Arial;
  font-size: 0.9vw;
}

h4 > button {
  background: palevioletred;
  color: white;
  font-size: 1.15vw;
  margin-left: 1vw;
  margin-top: -0.5vw;
  padding: 0.25vw 1vw;
  border: 0.5vw solid palevioletred;
  border-radius: 2vw;
  ;
}

h4 > button:hover {
  background: violet;
  border-color: violet;
}
`;

export const StyledFooter = styled.footer`
background-color: lightgray;
margin: 0;
padding: 0;
position: relative;

div {
  display: flex;
  text-align: center;
}

div > div {
  display: initial;
  text-align: center;
}

h1 {
  font-family: Helvetica;
  font-style: oblique;
  font-size: 2vw;
  margin: 1vw 0 0 0;
  margin-bottom: 0.05vw;
  padding-bottom: 0.05vw;
}

h3 {
  margin-bottom: 0.2vw;
  padding-bottom: 0.2vw;
  font-family: sans-serif;
  font-size: 1.3vw;
}

button {
  background: palevioletred;
  color: white;
  font-size: 1.3vw;
  padding: 0.25vw 1vw;
  border: 0.5vw solid palevioletred;
  border-radius: 2vw;
  margin: 0;
}

button:hover {
  background: violet;
  border-color: violet;
  ;
}

p{
  font-size: 1.25vw;
  font-family: sans-serif;
  font-style: oblique;
}
`;

export const VidWrapper = styled.div`
position: relative;
padding-bottom: 56.25%; /* 16:9 */
height: 0;

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
`;