import React from 'react';
import { useHistory } from 'react-router';
import CenterDiv from '../styled/CenterDiv';
import StyledMenu from '../styled/StyledMenu';
import * as ProjectsData from '../json/projects.json';
import * as ContactData from '../json/contactinfo.json';

import { StyledHeader, StyledBody, StyledFooter, VidWrapper } from '../styled/DivMakeup';

//#region MainPage (export) 
const MainPage = (): JSX.Element => {
  document.title = 'Burhan Topaloglu: Game/Web/App Developer';

  return (
    <>
      <StyledHeader>
        <IntroDiv />
      </StyledHeader>

      <StyledBody>
        <ProjectDiv />
      </StyledBody>

      <StyledFooter>
        <div id='about'>
          <CenterDiv>
            <h1>About me</h1>
            <p>Hey! I am Burhan Topaloglu. <br/> I live in Alkmaar, born 2001</p>
            <p>{'I just graduated at Mediacollege Amsterdam! >:D'} <br/>
             I am looking for a job! I qualify as a software developer, I can do games and webapps. <br/>
             Maybe learn to make mobile apps in the future? I started on Java after all..<br />
             Main programming languages: Unity C# and JavaScript</p>
             <p> I also have some basics of C++ and I persuing getting a feel for procedural C at the moment to learn something different from OOP; Data-Oriented-Design.</p>

             <p>My greatest skill in relation to my peers is in debugging and computational thinking. <br/>
             I am also a great project/scrum leader, I have been taught agile workflow.</p>
          </CenterDiv>
        </div>
        <div id='contact'>
          <ContactsDiv />
        </div>
      </StyledFooter>
      <Menu />
    </>
  );
}
//#endregion MainPage (export) 

//#region intro
const IntroDiv = (): JSX.Element => {
  return (
    <div>
      <h5>styling is not final, not every project is included</h5>
      <CenterDiv>
        <h1>Burhan Topaloglu</h1>
        <h3>Game and Web Developer</h3>
        <img src={`/media/burhan_profile.png`} width='80%' height='auto' alt={'burhan_profile.png'}></img>
        <h4>Welcome to my portfolio!</h4>
      </CenterDiv>
    </div>
  );
};
//#endregion intro

//#region projects
const ProjectDiv = (): JSX.Element => {

  if (ProjectsData.Projects.length === 0) return <CenterDiv>No Projects available</CenterDiv>;

  return <CenterDiv>
    {
      ProjectsData.Projects.map((project, _index) => {
        return (
          <React.Fragment key={_index}>
            <div id={project.tag}>
              <h2>{_index + 1}.</h2>
              <h2>{project.title}</h2>
              {project.img && project.img !== '' && //post image if image
                <img src={`/media/${project.img}`} width='100%' height='auto' alt={project.img}></img>
              }
              {project.ytwatchurl && //post youtube if youtube
                <VidWrapper>
                  <iframe src={`https://www.youtube.com/embed/${project.ytwatchurl}`} title="YouTube video player" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture; fullscreen"></iframe>
                </VidWrapper>
                }
              <h3>
                {project.text && // post text if text
                  <div>
                    <p>About this project: <br />
                      {
                        project.text.map((text) => { return <p>{text}</p> })
                      }
                    </p>
                  </div>
                }
              </h3>
              <h4>
                {project.githublink && //post github button if link
                  <button onClick={() => (window.open(project.githublink))}>Github Repository for {project.ghtext}</button>
                }
              </h4>
            </div>
            <br /> <br />
          </React.Fragment>
        );
      })
    }
  </CenterDiv>

};
//#endregion projects

//#region contacts
const ContactsDiv = (): JSX.Element => {
  if (ContactData.Contacts.length === 0) return <CenterDiv>No Contact info available</CenterDiv>;
  const contacts = ContactData.Contacts;

  return (
    <CenterDiv>
      <h1>Contact info</h1>
      <br/>
      {
        contacts.map((site, _index) => {
          return (
            <div id={site.tag} key={_index}>
              <h3>{site.name}</h3>
              <button onClick={() => (window.open(site.link))}>{site.linkText}</button>
            </div>);
        })
      }
    </CenterDiv>
  );
};
//#endregion contacts

//#region menu
const Menu = (): JSX.Element => {
  const history = useHistory();

  if (ProjectsData.Projects.length === 0) return <CenterDiv>No Projects available</CenterDiv>;

  return (
    <StyledMenu id="menu">
      <h2>
        {'Navigation '}
        <button onClick={() => {
          window.location.hash = 'contact';
          history.push('')
        }}>Contact info</button>
      </h2>
      <ul>
        <li><button onClick={() => {
          window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
          });
          history.push('');
        }}>Top</button></li>
        {
          ProjectsData.Projects.map((project) => {
            return (<li><button onClick={() => {
              window.location.hash = project.tag;
              history.push('');
            }}>{(project.menuTitle && project.menuTitle !== '') ? project.menuTitle : project.title}</button></li>);
          })
        }
        <li><button onClick={() => {
          window.location.hash = 'about';
          history.push('');
        }}>About me</button></li>
      </ul>
    </StyledMenu>
  );
};
//#endregion menu

export default MainPage;